function UserMenuItem({ user, onClick }: { user: any; onClick: () => void }) {
  const theme = useTheme();
  const currentUser = useUser();
  const isCurrentUser = user.uuid === currentUser.uuid;

  return (
    <MenuListItem onClick={onClick}>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          width: "100%",
        }}
      >
        <div
          style={{
            display: "flex",
          }}
        >
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
            }}
          >
            <IncognitoAvatar uuid={user.uuid} variant="sm" />
          </div>
          <Typography
            style={{
              marginLeft: "8px",
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              color: theme.baseTextHighEmphasis.val,
              fontSize: "14px",
            }}
          >
            {user.username}
          </Typography>
        </div>
        {isCurrentUser ? (
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              flexDirection: "column",
            }}
          >
            <Check
              style={{
                width: "20px",
                height: "20px",
                color: theme.accentBlue.val,
              }}
            />
          </div>
        ) : null}
      </div>
    </MenuListItem>
  );
}

type PopoverContext = {
  close: () => void;
  openSettings: () => void;
};
const _PopoverContext = createContext<PopoverContext | null>(null);

function PopoverProvider({ children, close, openSettings }: any) {
  return (
    <_PopoverContext.Provider
      value={{
        close,
        openSettings,
      }}
    >
      {children}
    </_PopoverContext.Provider>
  );
}

function usePopoverContext(): PopoverContext {
  const ctx = useContext(_PopoverContext);
  if (ctx === null) {
    throw new Error("Context not available");
  }
  return ctx;
}

export function IncognitoAvatar(props: {
  uuid: string;
  variant: "sm" | "md" | "lg";
}) {
  return (
    <Suspense fallback={null}>
      <_IncognitoAvatar {...props} />
    </Suspense>
  );
}

function _IncognitoAvatar({
  uuid,
  variant,
}: {
  uuid: string;
  variant: "sm" | "md" | "lg";
}) {
  const users = useAllUsers();
  const user = users.find((u) => u.uuid === uuid)!;
  const index = users.findIndex((u) => u.uuid === uuid);
  return (
    <_IncognitoAvatarFromUsername
      index={index}
      username={user.username}
      variant={variant}
    />
  );
}

export function _IncognitoAvatarFromUsername({
  index,
  username,
  variant,
}: {
  index: number;
  username: string;
  variant: "sm" | "md" | "lg";
}) {
  const initials = getInitials(username);
  const color = getAvatarColorFromIndex(index);
  const size = variant === "sm" ? 20 : variant === "md" ? 32 : 74;
  const fontSize =
    variant === "sm" ? "10px" : variant === "md" ? "14px" : "24px";

  return (
    <div
      style={{
        position: "relative",
        justifyContent: "center",
        display: "flex",
        flexDirection: "column",
        width: size,
        height: size,
      }}
    >
      <div
        style={{
          backgroundColor: color,
          opacity: 0.15,
          width: size,
          height: size,
          borderRadius: size / 2,
          position: "absolute",
          left: 0,
          top: 0,
          bottom: 0,
          right: 0,
        }}
      />
      <Typography
        style={{
          color: color,
          textAlign: "center",
          fontSize,
        }}
      >
        {initials}
      </Typography>
    </div>
  );
}

function getInitials(username: string): string {
  const components = username.split(" ").filter(Boolean);
  let initials = [...components[0]][0];
  if (components.length > 1) {
    initials += [...components[1]][0];
  }
  return initials.toUpperCase();
}
